import styled from "styled-components"

export default function Label({text,height ,borderColor ,color,bgColor,borderRadius = '50%',fontSize,icon,className}) {
  const Wrapper = styled.div`
    padding : 0 8px;
    height : ${height}px;
    color : ${color};
    border-color : ${borderColor};
    background-color : ${bgColor};
    border-radius : ${borderRadius};
    font-size : ${fontSize}px;
    text-align : center;
    .main {
      display : flex;
      align-items : center;
      justify-content : center;
    }
    img {
      margin-right :8px;
    }
  `
  return (
    <Wrapper className={className}>
      <span className='main'>
        {icon && <img src={icon} alt='' />}
        {text}
      </span>
    </Wrapper>
  )
}