export const getPoolConfigList = async (env, type=null, includeOpenPools = false) => {
  return catchApiError(async () => {
    env = checkEnv(env);
    let res = getJsonConfig(env);
    // add v1 config
    // res= res.concat(getPoolV1ConfigList(env));
    if (includeOpenPools) {
      const result = await getPoolOpenConfigList();
      if (result.success) {
        res = [...res, ...result.response.data];
      } else {
        console.log(result.response.error && result.response.error.message);
      }
    }
    if (!!type) {
      res = res.filter((c) => c.version === type)
    }
    return res;
  }, [env, type, includeOpenPools]);
};
