import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { PRIMARY, SECONDARY } from '../../utils/Constants';
import classNames from 'classnames';
import Spinner from '../Spinner/Spinner';

export default function Button({label,fontColor = '#E0ECFF',type = PRIMARY,bgColor,defaultBorderColor = '#203B60',borderSize = 1, outline = false,isSelected = false,outlineColor = '#CD7A37',iconsrc ,onClick,width = 158,fontSize = 14,fontWeight = 'normal',height = 48,className,styles = {}}){
  const [pending, setPending] = useState(false)
  let backgroundColor ;
  if(bgColor) {
    backgroundColor = bgColor;
  } else if(type === PRIMARY) {
    backgroundColor = '#3756CD';
  } else if(type === SECONDARY){
    backgroundColor = '#203B60' 
  }
  const borderColor = outline ? outlineColor : 'none';
  const Wrapper = styled.div`
    display : flex;
    align-items : center;
    background-color : ${backgroundColor};
    font-size : ${fontSize}px;
    font-weight : ${fontWeight};
    color : ${fontColor};
    width : ${width}px;
    height : ${height}px;
    justify-content: center;
    border-radius: 4px;
    border: ${borderSize}px solid ${defaultBorderColor};
    &:hover {
      border : 1px solid ${borderColor};
      cursor: pointer;
    }
    &.selected {
      border : 1px solid ${borderColor};
    }
  `  
  const click = async () => {
    if(onClick){
      setPending(true);
      const result = await onClick();
      setPending(false)
    }
  }
  const clazz = classNames(className,{
    selected : isSelected
  })
  return (
    <Wrapper className={clazz} style={{...styles}} onClick={click}>
      {iconsrc && <img src={iconsrc} alt=''/>}{pending && <Spinner/>}{label}
    </Wrapper>
  )
}